import React, { useState } from 'react';

function Menubar() {

	console.log("[Menubar.js] render()");

	const [isOpen, setIsOpen] = useState(false);

	const toggleSubMenu = (e) => {
		e.preventDefault();
		setIsOpen(!isOpen);
	};
	
	return (
		<nav>
			<ul>	{/* href   Router.js  . */}
				<li className={`has_sub ${isOpen ? 'open' : ''}`}>
					<a href="#" className={isOpen ? 'on' : ''} onClick={toggleSubMenu}> <span> </span></a>
					<ul>
						<li><a href="/bbslist" className={isOpen ? 'on' : ''}></a></li>
						<li><a href="/employees" className={isOpen ? 'on' : ''}></a></li>
						<li><a href="/user" className={isOpen ? 'on' : ''}></a></li>
						<li><a href="#" onClick={() => alert(' .')}></a></li>
						<li><a href="#" onClick={() => alert(' .')}></a></li>
					</ul>
				</li>
				<li className={`has_sub ${isOpen ? 'open' : ''}`}>
					<a href="#" className={isOpen ? 'on' : ''} onClick={toggleSubMenu}> <span> </span></a>
					<ul>
						<li><a href="#" onClick={() => alert(' .')}></a></li>
						<li><a href="#" onClick={() => alert(' .')}></a></li>
						<li><a href="#" onClick={() => alert(' .')}></a></li>
					</ul>
				</li>
			</ul>
		</nav>
	);
}

export default Menubar;